	display: flex;
	flex-direction: column;
	align-items: center;

	width: 100%;
}

.maquinas-contenedor {
	display: flex;
	flex-direction: column;
}

.toolbox-maquinas {
	width: 150px;
	height: 250px;	
	margin: 4px 0 10px 0;
