}

.acciones-contenedor {
	display: flex;
	flex-direction: column;
}

.toolbox-acciones {
	width: 150px;
	height: 250px;	
	margin: 4px 0 10px 0;
  background-color: #f1efff;
}

.borde-piola {
	border-radius: 4px;
}