body {
  margin: 0;
  padding: 0;

  background-color: #2d2d3e;

  font-size: 16px;
  font-weight: bold;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  color: #f18730;
}

.app-contenedor {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "header header header" "sidebar-left main sidebar-right" "footer footer footer"; }

.fabrica-contenedor {
  grid-area: main;
  display: grid;
  grid-template-columns: repeat(10, 55px);
  grid-template-rows: repeat(10, 55px);
  width: 550px;
  height: 550px;
  border: 1px solid #2d2d3e; }

.celda {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f1efff;
  border: 1px solid #cedae9; }

.toolbox-contenedor {
  grid-area: sidebar-left;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; }

.maquinas-contenedor {
  display: flex;
  flex-direction: column; }

.toolbox-maquinas {
  width: 150px;
  height: 250px;
  margin: 4px 0 10px 0;
  background-color: #f1efff; }

.acciones-contenedor {
  display: flex;
  flex-direction: column; }

.toolbox-acciones {
  width: 150px;
  height: 250px;
  margin: 4px 0 10px 0;
  background-color: #f1efff; }

.borde-piola {
  border-radius: 4px; }

